@tailwind base;
@tailwind components;
@tailwind utilities;

/* scroll bar stuling  */


/* Works on Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: rgb(143, 143, 143) whitesmoke;
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: whitesmoke;
}

*::-webkit-scrollbar-thumb {
  background: -webkit-linear-gradient(top, #5f5f5f, #5f5f5f);
  background: linear-gradient(to bottom, #5f5f5f, #5f5f5f);
  border-radius: 20px;
  border: 3px solid #646363;
}


.loading-indicator:before {
    content: '';
    background: #00000080;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
  }
  
  .loading-indicator:after {
    content: ' ';
    position: fixed;
    top: 40%;
    left: 45%;
    z-index: 10010;
    color:white;
    text-align:center;
    font-weight:bold;
    font-size:1.2rem;        
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #0474bf; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
}